import React from 'react';
import Notepad from '../Modules/Notepad';
import TodoList from '../Modules/TodoList';
import AIChat from '../Modules/AIChat';
import PomodoroTimer from '../Modules/PomodoroTimer';
import TaskManager from '../Modules/TaskManager';
import ProgressMonitor from '../Modules/ProgressMonitor';
import { RainbowCard } from '../ui/rainbow-card';

const MainContent = ({ activeModule }) => {
  const renderContent = () => {
    switch (activeModule) {
      case 'welcome':
        return (
          <div className="text-center py-12">
            <h2 className="text-3xl font-bold text-gray-900 mb-6">
               
            </h2>
            <p className="text-gray-600 mb-8 max-w-2xl mx-auto">
              AI
            </p>
            
            <div className="grid grid-cols-1 md:grid-cols-2 gap-6 max-w-4xl mx-auto">
              <div className="card hover:shadow-md transition-shadow cursor-pointer">
                <h3 className="text-lg font-semibold mb-2 flex items-center">
                   <span className="ml-2"></span>
                </h3>
                <p className="text-gray-600">Markdown</p>
                <div className="mt-3 text-sm text-primary-600"> </div>
              </div>
              
              <div className="card hover:shadow-md transition-shadow cursor-pointer">
                <h3 className="text-lg font-semibold mb-2 flex items-center">
                   <span className="ml-2"></span>
                </h3>
                <p className="text-gray-600"></p>
                <div className="mt-3 text-sm text-primary-600"> </div>
              </div>
              
              <div className="card hover:shadow-md transition-shadow cursor-pointer">
                <h3 className="text-lg font-semibold mb-2 flex items-center">
                   <span className="ml-2">AI </span>
                </h3>
                <p className="text-gray-600">API Key</p>
                <div className="mt-3 text-sm text-primary-600"> </div>
              </div>
              
              <div className="card hover:shadow-md transition-shadow cursor-pointer">
                <h3 className="text-lg font-semibold mb-2 flex items-center">
                   <span className="ml-2"></span>
                </h3>
                <p className="text-gray-600"></p>
                <div className="mt-3 text-sm text-primary-600"> </div>
              </div>
            </div>
            
            <div className="mt-8">
              <p className="text-sm text-gray-500">
                 
              </p>
            </div>
          </div>
        );
      case 'notes':
        return <Notepad />;
      case 'todos':
        return <TodoList />;
      case 'tasks':
        return <TaskManager />;
      case 'progress':
        return <ProgressMonitor />;
      case 'chat':
        return <AIChat />;
      case 'pomodoro':
        return <PomodoroTimer />;
      default:
        return (
          <div className="p-8 text-center">
            <h2 className="text-2xl font-bold mb-4"></h2>
            <p className="text-gray-600"></p>
          </div>
        );
    }
  };

  return (
    <main className="flex-1 overflow-hidden">
      <div className="h-full">
        {activeModule === 'notes' || activeModule === 'chat' ? (
          <RainbowCard className="h-full overflow-hidden">
            {renderContent()}
          </RainbowCard>
        ) : (
          <div className="h-full p-6">
            <RainbowCard className="h-full overflow-y-auto">
              {renderContent()}
            </RainbowCard>
          </div>
        )}
      </div>
    </main>
  );
};

export default MainContent;